


































var _self, cate = 0, page = 1;
export default {
	data() {
		return {
			categories:[{cateId : 0, name : "全部"}],
			cateCurrentIndex : 0,
			artList:[]
		}
	},
	onLoad() {
		_self = this;
		uni.request({
			url: _self.apiServer,
			method: 'GET',
			data: {},
			success: res => {
				console.log(JSON.stringify(res));
			},
			fail: (err) => {
				console.log(err);
			},
			complete: () => {}
		});
	},
	onLoad() {
		_self = this;
		// 加载文章分类
		uni.request({
			url: this.apiServer+'category&m=index',
			method: 'GET',
			success: res => {
				if(res.data.status == 'ok'){
					// 把数据格式整理为 picker 支持的格式 ['分类名', ...]
					var categories = res.data.data;
					for(var k in categories){
						_self.categories.push({cateId : k, name : categories[k]});
					}
					// 记录分类信息
					_self.catiesFromApi = categories;
				}
			}
		});
		// 加载全部文章
		this.getNewsList();
	},
	//下拉刷新
	onPullDownRefresh: function(){
		page = 1;
		this.artList = [];
		this.getNewsList();
	},
	// 加载更多
	onReachBottom:function(){
		this.getNewsList();
	},
	methods: {
		tabChange : function(e){
			console.log(e);
			var cateid = e.currentTarget.dataset.cateid;
			var index = e.currentTarget.dataset.index;
			page = 1;
			this.cateCurrentIndex = index;
			cate = cateid;
			this.artList = [];
			this.getNewsList();
		},
		getNewsList : function(){
			uni.showLoading({'title':"加载中..."});
			uni.request({
				url: this.apiServer + 'art&m=getList&cate='+cate+'&page='+page,
				method: 'GET',
				success: res => {
					if(res.data.status == 'empty'){
						uni.showToast({
							title:"已经加载全部新闻",
							icon: "none"
						});
					}else if(res.data.status == 'ok'){
						//整理新闻信息
						var newsList = res.data.data;
						for(var i = 0; i < newsList.length; i++){
							// 把图片分离出来
							var imgs = [];
							var content = newsList[i].art_content;
							content = JSON.parse(content);
							for(var ii = 0; ii < content.length; ii++){
								if(content[ii].type == 'image'){
									imgs.push(content[ii].content);
								}
							}
							newsList[i].art_content = imgs;
						}
						//填充数据
						_self.artList = _self.artList.concat(newsList);
						uni.hideLoading();
						page++;
					}
				},
				complete:function(){
					uni.stopPullDownRefresh();
				}
			});
		}
	}
}
